.kart-loader {
	position: relative;
	margin: 213px auto;
	display:block;
	width:106px;
}

.sheath {
	position: absolute;
	transform-origin: 50% 50%;
		-o-transform-origin: 50% 50%;
		-ms-transform-origin: 50% 50%;
		-webkit-transform-origin: 50% 50%;
		-moz-transform-origin: 50% 50%;
}

.segment {
	background-color: rgb(19,223,207);
